import React from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';

import { CreateAlumnsScreen } from '../components/alumnos/CreateAlumnsScreen';
import { SeeAlumnsScreen } from '../components/alumnos/SeeAlumnsScreen';
import { HomeScreen } from '../components/ui/HomeScreen';
import { NavbarComponent } from '../components/ui/NavbarComponent';
  
export const AppRouter = () => {

    return (
        <Router>
            <NavbarComponent />
                <Switch>
                    
                    <Route exact path="/ver" component={ SeeAlumnsScreen } />
                   
                    <Route exact path="/crear" component={ CreateAlumnsScreen } />

                    <Route exact path="/" component={ HomeScreen } />
                    
                    <Redirect to="/" />

                </Switch>

        </Router>
    )
}