import React, { ChangeEvent, SyntheticEvent, useEffect, useState } from 'react'

import { generateFromString } from 'generate-avatar';

import { ALUMNOS_DE_2002, ALUMNOS_DE_BATAN_CON_SANGRE_RH_NEGATIVA, ALUMNOS_DE_JUAN_B_JUSTO, ALUMNOS_EN_BATAN, ALUMNOS_ORDENADOS_POR_DNI_ASC, IAlumno } from '../../types/types';
import { getAlumnos } from '../../helpers/getAlumnos';

import '../../styles/SeeAlumnsScreen.css';
import { DeleteAlumnFab } from '../ui/DeleteAlumnFab';
import { ModifyAlumnModal } from './ModifyAlumnModal';
import { RootStateOrAny, useDispatch, useSelector } from 'react-redux';
import { uiOpenModal } from '../../actions/ui';
import { alumnoSetActive } from '../../actions/alumnos';
import { FormHelperText, InputLabel, MenuItem, Select } from '@material-ui/core';

export const SeeAlumnsScreen = () => {

  const dispatch = useDispatch();

  const { activeAlumno } = useSelector(( state: RootStateOrAny ) => state.alumno);

  const handleDoubleClick = ( e: SyntheticEvent) => {
    dispatch( uiOpenModal() );
  }

  const onSelectEvent = (e: SyntheticEvent, alumno: IAlumno) => {
    dispatch( alumnoSetActive(alumno) );
  }

  interface Provider {
    data: any,
    loading: boolean
  }

  /**
   * Del state solo me interesa la data, asi que desestructuro el objeto, y renombro a data como alumnos.
   */
  const [ { data: alumnos }, setState ] = useState<Provider>({
    data: [],
    loading: true
  });

  const [ filtro, setFiltro ] = useState('');

  const [ orden, setOrden ] = useState('');

  const handleChange = ( e: ChangeEvent<{ value: unknown }> ) => {
    setFiltro( e.target.value as string );
  }

  const handleOrderChange = ( e: ChangeEvent<{ value: unknown }>  ) => {
    setOrden( e.target.value as string )
  }

  const juan_b_justo = /justo [0-9]/;

  const alumnosFiltrados = alumnos.filter( ( alumno: IAlumno ) => {
    switch ( filtro ) {
      case ALUMNOS_EN_BATAN:
        
        return alumno.cod_postal === 7601;

      case ALUMNOS_DE_2002:

        return alumno.fecha_nac_alumno.includes('2002');
    
      case ALUMNOS_DE_BATAN_CON_SANGRE_RH_NEGATIVA:

        return ( alumno.grupo_sang_alumno ).toLowerCase() === 'rh-';

      case ALUMNOS_DE_JUAN_B_JUSTO:
        return juan_b_justo.test( ( alumno.dom_alumno ).toLowerCase() );

      default:
        return alumno;
    }
  });

  // De esta forma, ordenamos a los alumnos según el campo elegido
  const alumnosPreparados = alumnosFiltrados.sort( ( a: any , b: any ) => { 
    switch ( orden ) {
      case ALUMNOS_ORDENADOS_POR_DNI_ASC:
        console.log(`${a.dni_alumno} y su tipo es ${ typeof a.dni_alumno }`);
        return a.dni_alumno - b.dni_alumno;
    
      default:
        return 1;
    }
  });


  useEffect(() => {
    getAlumnos()
      .then(alumnos => {
        setState({
          data: alumnos,
          loading: false
        });
      })
  }, []);

  return (
    <div className="container mt-3">
      <div className="grid-container">
        <div className="grid-child">
          <InputLabel id="demo-simple-select-placeholder-label-label">
            Filtrar alumnos
          </InputLabel>
          <Select
            value={ filtro }
            onChange={ handleChange }
            displayEmpty
            inputProps={{ 'aria-label': 'Without label' }}
          >
            <MenuItem value="" disabled>
              Elija lo que más le guste
            </MenuItem>
            <MenuItem value=""> Filtro desactivado </MenuItem>
            <MenuItem value="filtro_batan"> Filtrar: Viven en Batán </MenuItem>
            <MenuItem value="filtro_2002"> Filtrar: Nacidos en 2002 </MenuItem>
            <MenuItem value="filtro_batan_y_rh-"> Filtrar: Viven en Batán y grupo sanguíneo RH- </MenuItem>
            <MenuItem value="filtro_juanbjusto"> Filtrar: Viven en Av. J.B. Justo </MenuItem>
          </Select>
          <FormHelperText> Hola Mundo </FormHelperText>
        </div>
        <div className="grid-child">
          <InputLabel id="demo-simple-select-placeholder-label-label">
            Ordenar alumnos
          </InputLabel>
          <Select
            value={ orden }
            onChange={ handleOrderChange }
            displayEmpty
            inputProps={{ 'aria-label': 'Without label' }}
          >
            <MenuItem value="" disabled>
              Elija lo que más le guste
            </MenuItem>
            <MenuItem value=""> Orden por defecto </MenuItem>
            <MenuItem value="orden_dni_asc"> Ordenar: DNI ASCENDENTE </MenuItem>
          </Select>
        </div>
      </div>
      
      <hr/>
      
      <div className="card-columns">
        {  
          alumnosPreparados.map((alumno: IAlumno) => (
            <div 
              className="card clickable" 
              key={ alumno.leg_alumno }
              onDoubleClick={ handleDoubleClick }
              onClick={ (e: SyntheticEvent) => {
                onSelectEvent(e, alumno)
              } }
            >
            <img 
              className="card-img-top img-fluid" 
              src={ `data:image/svg+xml;utf8,${ generateFromString( alumno.leg_alumno.toString() ) }` } 
              alt="Avatar del alumno"
            />  
              <div className="card-block m-3">
                <h4 className="card-title"> { alumno.apyn_alumno } </h4>
                <hr/>
                <p className="card-text"> - Domicilio:  { alumno.dom_alumno } ({ alumno.cod_postal }) </p>
                <p className="card-text"> - DNI:  { alumno.dni_alumno } </p>
                <p className="card-text"> - Teléfono Móvil: { alumno.tel_movil_alumno } </p>
                <p className="card-text"> - Fecha de nacimiento: { alumno.fecha_nac_alumno } </p>
                <p className="card-text"> - Grupo sanguíneo: { alumno.grupo_sang_alumno } </p>
              </div>
            </div>
          ))
        }
      </div>

      <ModifyAlumnModal />        

      {
          ( activeAlumno ) && <DeleteAlumnFab /> 
      }      
    
    </div>
  )

}
