import { ALUMNOS_EN_BATAN, ALUMNOS_LOADED, ALUMNO_CLEAR_ACTIVE, ALUMNO_DELETED, ALUMNO_SET_ACTIVE, ALUMNO_UPDATE, IAlumno, IUpdateAlumno } from "../types/types";

const initialState = {

    alumnos: [],
    activeAlumno: null as null | IAlumno

};

export const alumnoReducer = ( state = initialState, action: any ) => {
    switch (action.type) {
        
        case ALUMNO_SET_ACTIVE:
            return {
                ...state,
                activeAlumno: action.payload
            }

        case ALUMNO_CLEAR_ACTIVE:
            return {
                ...state,
                activeAlumno: null
            }

        case ALUMNO_UPDATE:
            return {
                ...state,
                alumnos: state.alumnos.map(
                    ( alumno: IUpdateAlumno ) => ( alumno.leg_alumno === action.payload.leg_alumno ) ? action.payload : alumno
                )
            }

        case ALUMNO_DELETED:
            return {
                ...state,
                alumnos: state.alumnos.filter(
                    ( alumno: IAlumno ) => ( alumno.leg_alumno !== state?.activeAlumno?.leg_alumno ) 
                ),
                activeAlumno: null
            }

        case ALUMNOS_LOADED:
            return {
                ...state,
                alumnos: [ ...action.payload ]
            }

        default:
            return state;
    }
}