import { UI_OPEN_MODAL, UI_CLOSE_MODAL } from '../types/types';

const initialState = {
    modalOpen: false,
}

export const uiReducer = ( state = initialState, action: any ) => {

    switch ( action.type ) {
        case UI_OPEN_MODAL:
            return {
                ...state,
                modalOpen: true
            };
        
        case UI_CLOSE_MODAL:
            return {
                ...state,
                modalOpen: false
            }

        default:
            return state;
    }

}