import * as Yup from 'yup';

/**
 * Para más info sobre validación de formularios ver esta página: 
 * https://formik.org/docs/guides/validation
 */  

export const CreateAlumnSchema = Yup.object().shape({

    leg_alumno: Yup
        .number()
        .integer('¡Solo enteros!')
        .positive('¡Tiene que ser mayor a cero, capo!')
        .required('Este campo es obligatorio'),
    apyn_alumno: Yup
        .string()
        .min(3, '¡Muy corto!')
        .max(50, '¡Muy largo!')
        .required('Este campo es obligatorio'),
    dom_alumno: Yup
        .string()
        .min(8, '¡Muy corto!')
        .max(50, '¡Muy largo!')
        .required('Este campo es obligatorio'),
    cod_postal: Yup
        .number()
        .required('Este campo es obligatorio'),
    fecha_nac_alumno: Yup
        .date()
        .required('Este campo es obligatorio'),
    email_alumno: Yup
        .string()
        .email('Formato de correo electrónico inválido')
        .max(50, '¡Muy largo!')
        .required('Este campo es obligatorio'),
    grupo_sang_alumno: Yup
        .string()
        .max(50, '¡Muy largo!')
        .required('Pone lo que quieras, pero pone algo. ¡Por favor!'),
    tel_fijo_alumno: Yup
        .string()
        .max(50, '¡Muy largo!')
        .required('Pone lo que quieras, pero pone algo. ¡Por favor!'),
    tel_movil_alumno: Yup
        .string()
        .max(50, '¡Muy largo!')
        .required('Este campo es obligatorio'),
    dni_alumno: Yup
        .number()
        .min(1000000, 'Ni Pablo Escobar se animó a tanto')
        .max(1000000000000, 'Nuevo título desbloqueado: Falsificador serial de documentos')
        .required('No hay escape. Este campo es obligatorio')
});