import React, { SyntheticEvent, useEffect, useState } from 'react'
import Modal from 'react-modal';
import { RootStateOrAny, useDispatch, useSelector } from 'react-redux';
import { clearActiveAlumno, startAlumnoUpdate } from '../../actions/alumnos';
import { uiCloseModal } from '../../actions/ui';

const customStyles = {
    content : {
      top                   : '50%',
      left                  : '50%',
      right                 : 'auto',
      bottom                : 'auto',
      marginRight           : '-50%',
      transform             : 'translate(-50%, -50%)'
    }
};

Modal.setAppElement('#root')

export const ModifyAlumnModal: React.FC = () => {
    
    const { modalOpen } = useSelector(( state: RootStateOrAny ) => state.ui )

    const { activeAlumno } = useSelector(( state: RootStateOrAny ) => state.alumno )

    const dispatch = useDispatch();

    const [ formValues, setFormValues ] = useState({
        leg_alumno: 0,
        apyn_alumno: '',
        dom_alumno: '',
        cod_postal: 7600,
        tel_fijo_alumno: '',
        tel_movil_alumno: '',
    });

    const [ isDomicilioValid, setIsDomicilioValid ] = useState(true);

    const [ isTelefonoFijoValid, setIsTelefonoFijoValid] = useState(true);

    const [ isTelefonoMovilValid, setIsTelefonoMovilValid ] = useState(true);

    const handleInputChange = ( { target }: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLSelectElement> ) => {

        setFormValues({
            ...formValues,
            [ target.name ]: target.value
        })

    }

    const { apyn_alumno, dom_alumno, cod_postal, tel_fijo_alumno, tel_movil_alumno } = formValues;

    useEffect(() => {
        if ( activeAlumno ) {
            setFormValues( activeAlumno );
        }
    }, [ activeAlumno, setFormValues ])

    const closeModal = () => {
        dispatch( uiCloseModal() );
        dispatch( clearActiveAlumno() )
    }

    const handleSubmit = ( e: SyntheticEvent ) => {
        e.preventDefault();

        if ( ( dom_alumno.length <= 8 || dom_alumno.length >= 50 ) && dom_alumno.length !== 0 ) {
            return setIsDomicilioValid(false);
        }

        if ( tel_fijo_alumno.length >= 50 && tel_fijo_alumno.length !== 0) {
            return setIsTelefonoFijoValid(false);
        }
        
        if ( tel_movil_alumno.length >= 50 && tel_movil_alumno.length !== 0) {
            return setIsTelefonoMovilValid(false);
        }

        dispatch( startAlumnoUpdate( formValues ) );

        setIsDomicilioValid(true);
        setIsTelefonoFijoValid(true);
        setIsTelefonoMovilValid(true);

        window.location.href = '/ver';

        closeModal();

    }
 
    return (
        <Modal
            isOpen={ modalOpen }
            closeTimeoutMS={ 200 }
            onRequestClose={ closeModal }
            style={ customStyles }
            className="modal"
            overlayClassName="modal-fondo"
        >
            <h1 className="m-3"> { apyn_alumno } </h1>
            <hr/>
                
            <form 
                className="container"
                onSubmit={ handleSubmit }
            >

                    <div className="form-group">
                        <label> Domicilio </label>
                        <input 
                            type="text"
                            className={ `form-control ${ !isDomicilioValid && 'is-invalid' } ` }
                            name="dom_alumno"
                            autoComplete="off"
                            value={ dom_alumno }
                            onChange={ handleInputChange } 
                        />
                    </div>

                    <div className="form-group">
                        <label> Código Postal </label>
                        <select
                            name="cod_postal"
                            className="form-control"
                            value={ cod_postal } 
                            onChange={ handleInputChange } 
                        >
                            <option value={ 7600 }> Mar del Plata </option>
                            <option value={ 7601 }> Batán </option>
                            <option value={ 8000 }> Bahía Blanca </option>
                        </select>
                    </div>

                    <div className="form-group">
                        <label> Teléfono fijo </label>
                        <input 
                            type="text"
                            className={ `form-control ${ !isTelefonoFijoValid && 'is-invalid' } ` }
                            name="tel_fijo_alumno"
                            autoComplete="off"
                            value={ tel_fijo_alumno }
                            onChange={ handleInputChange }  
                        />
                    </div>

                    <div className="form-group">
                        <label> Teléfono móvil </label>
                        <input 
                            type="text"
                            className={ `form-control ${ !isTelefonoMovilValid && 'is-invalid' } ` }
                            name="tel_movil_alumno"
                            autoComplete="off"
                            value={ tel_movil_alumno }
                            onChange={ handleInputChange }  
                        />
                    </div>

                    <button
                        type="submit"
                        className="btn btn-outline-primary btn-block"
                    >
                        <i className="far fa-save"></i>
                        <span> Guardar</span>
                    </button>

            </form>



        </Modal>
    )
}
