import { Dispatch } from 'redux';
import Swal from 'sweetalert2';
import { globalVariables } from '../config/global';
import { ALUMNOS_LOADED, ALUMNO_ADD_NEW, ALUMNO_CLEAR_ACTIVE, ALUMNO_DELETED, ALUMNO_SET_ACTIVE, ALUMNO_UPDATE, IAlumno, IUpdateAlumno } from '../types/types';

export const alumnoAddNew = ( alumno: IAlumno ) => ({
    type: ALUMNO_ADD_NEW,
    payload: alumno
});

export const alumnoSetActive = ( alumno: IAlumno ) => ({
    type: ALUMNO_SET_ACTIVE,
    payload: alumno
});

export const clearActiveAlumno = () => ({
    type: ALUMNO_CLEAR_ACTIVE
});

export const alumnosStartLoading = ( ) => {
    return async ( dispatch: Dispatch ) => {
        try {
            
            const resp = await fetch(`${ globalVariables.url }/api/alumnos/`);
            const data = await resp.json();

            const alumnos = data.alumnos;

            dispatch( alumnosLoaded( alumnos ) );

        } catch (error) {
            console.log(error);
        }
    }
}

const alumnosLoaded = ( alumnos: IAlumno[] ) => ({
    type: ALUMNOS_LOADED,
    payload: alumnos
})

export const startAlumnoUpdate = ( alumno: IUpdateAlumno ) => {
    return async ( dispatch: Dispatch ) => {
        try {
            const resp = await fetch(`${ globalVariables.url }/api/alumnos/${ alumno.leg_alumno }`, {
                method: 'PUT',
                body: JSON.stringify(alumno),
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const data = await resp.json();
            if ( data.ok ) {
                Swal.fire({
                    title: '¡Alumno actualizado exitosamente!',
                    text: 'Los cambios tomarán efecto al cerrar este alerta',
                    icon: 'success'
                });
                dispatch( alumnoUpdated( alumno ) );
            } else {
                Swal.fire({
                    title: 'Algo salió mal...',
                    text: data.msg,
                    icon: 'error'
                });
            }

        } catch (error) {
            console.log(error);
        }
    }
}

export const alumnoStartDelete = () => {
    return async ( dispatch: Dispatch, getState: () => any) => {

        const { leg_alumno } = getState().alumno.activeAlumno;

        try {
            const resp = await fetch(`${ globalVariables.url }/api/alumnos/${ leg_alumno }`, {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const data = await resp.json();
            if ( data.ok ) {
                dispatch( alumnoDeleted() );
            } else {
                Swal.fire({
                    title: 'Algo salió mal...',
                    text: data.msg,
                    icon: 'error'
                });
            }

        } catch (error) {
            console.log(error)
        }
    }
}

const alumnoUpdated = ( alumno: IUpdateAlumno ) => ({
    type: ALUMNO_UPDATE,
    payload: alumno
});

const alumnoDeleted = () => ({
    type: ALUMNO_DELETED
});