import { IAlumno } from '../types/types';
import { globalVariables } from '../config/global';

const alumnosUrl = `${ globalVariables.url }/api/alumnos`;

export const getAlumnos = async () => { 
    const resp = await fetch(alumnosUrl);
    if ( !resp.ok ) throw new Error('No se pudo realizar la petición');
    const { alumnos: data } = await resp.json();
    const alumnos = data.map((alumno: IAlumno)  => {
        return {
        leg_alumno: alumno.leg_alumno, 
        apyn_alumno: alumno.apyn_alumno, 
        dom_alumno: alumno.dom_alumno,
        cod_postal: alumno.cod_postal,
        fecha_nac_alumno: alumno.fecha_nac_alumno,
        email_alumno: alumno.email_alumno,
        grupo_sang_alumno: alumno.grupo_sang_alumno,
        tel_fijo_alumno: alumno.tel_fijo_alumno,
        tel_movil_alumno: alumno.tel_movil_alumno,
        dni_alumno: alumno.dni_alumno
        }
    });

    return alumnos;

}
