import React from 'react';

import { Formik, Field, Form } from 'formik';
import {
    TextField,
    Button,
    Select,
    MenuItem
  } from '@material-ui/core';
import Swal from 'sweetalert2';

import { IAlumno } from '../../types/types';
import { CreateAlumnSchema } from '../../schemas/AlumnSchema';

import '../../styles/CreateAlumnsScreen.css';
import { globalVariables } from '../../config/global';

export const CreateAlumnsScreen = () => {

    return (
        <div className="container mt-3">
            <h1> Dar de alta alumno </h1>
            <hr/>
            <Formik
                validateOnChange={ true }
                initialValues={{
                    leg_alumno: 1234,
                    apyn_alumno: '',
                    dom_alumno: '',
                    cod_postal: 7600,
                    fecha_nac_alumno: '',
                    email_alumno: '',
                    grupo_sang_alumno: '',
                    tel_fijo_alumno: '',
                    tel_movil_alumno: '',
                    dni_alumno: 43742273
                }}
                validationSchema={ CreateAlumnSchema }
                onSubmit={ async ( values: IAlumno, { setSubmitting } ) => {
                    setSubmitting(true);
                    // make async call (in this case, a post)
                    const resp = await fetch(`${ globalVariables.url }/api/alumnos`, {
                            method: 'POST',
                            body: JSON.stringify(values),
                            headers: {
                                'Content-Type': 'application/json'
                            }
                        });
                    const data = await resp.json();
                    if ( data.ok ) {
                        Swal.fire({
                            title: '¡Alumno cargado con éxito!',
                            text: 'Será redirigido a la página principal',
                            icon: 'success'
                        }).then(() => {
                            window.location.href = '/ver';
                        });
                    } else {
                        Swal.fire({
                            title: 'Algo salió mal...',
                            text: data.msg,
                            icon: 'error'
                        });
                    }
                    setSubmitting(false);
                }}
            >
                {({ values, isSubmitting, errors, touched }) => (
                    <Form>

                        <div className="form-group">
                            <label className="control-label col-sm-2"> Legajo </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="legajo" 
                                    name="leg_alumno" 
                                    placeholder="123"
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    { ( errors.leg_alumno && touched.leg_alumno ) &&
                                        <div className="form-error m-1"> { errors.leg_alumno } </div> 
                                    }
                            </div>
                        </div>

                        <div className="form-group">
                            <label className="control-label col-sm-2"> Apellido y nombre </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="apyn" 
                                    name="apyn_alumno" 
                                    placeholder="Iván Silvero"
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    { ( errors.apyn_alumno && touched.apyn_alumno ) &&
                                        <div className="form-error m-1"> { errors.apyn_alumno } </div>
                                    }
                            </div>
                        </div>

                        <div className="form-group">
                            <label className="control-label col-sm-2"> Domicilio </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="domicilio" 
                                    name="dom_alumno" 
                                    placeholder="Juan B. Justo 4287"
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    {
                                        ( errors.dom_alumno && touched.dom_alumno ) &&
                                            <div className="form-error m-1"> { errors.dom_alumno } </div>
                                    }
                            </div>
                        </div>
                        
                        <div className="form-group">
                            <label className="control-label col-sm-2"> Código Postal </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="codigo_postal" 
                                    name="cod_postal"
                                    type="select"
                                    as={ Select }
                                    >
                                        <MenuItem value={ 7600 }> Mar del Plata </MenuItem>
                                        <MenuItem value={ 7601 }> Batán </MenuItem>
                                        <MenuItem value={ 8000 }> Bahía Blanca </MenuItem>
                                </Field>
                                    {
                                        ( errors.cod_postal && touched.cod_postal ) &&
                                            <div className="form-error m-1"> { errors.dom_alumno } </div>
                                    }
                            </div>
                        </div>

                        <div className="form-group">
                            <label className="control-label col-sm-2"> Fecha de nacimiento </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="fecha_de_nacimiento" 
                                    name="fecha_nac_alumno" 
                                    placeholder="Formato: YYYY-MM-DD. Por ejemplo, 2020-10-23" 
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    {
                                        ( errors.fecha_nac_alumno && touched.fecha_nac_alumno ) &&
                                            <div className="form-error m-1"> { errors.fecha_nac_alumno } </div>
                                    }
                            </div>
                        </div>
                        
                        <div className="form-group">
                            <label className="control-label col-sm-2"> Correo electrónico </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    type="email" 
                                    id="email" 
                                    name="email_alumno" 
                                    placeholder="ejemplo@gmail.com" 
                                    autoComplete="off"
                                    as={ TextField }
                                    />
                                    {
                                        ( errors.email_alumno && touched.email_alumno ) &&
                                            <div className="form-error m-1"> { errors.email_alumno } </div>
                                    }
                            </div>
                        </div>
                        
                        <div className="form-group">
                            <label className="control-label col-sm-2"> Grupo sanguíneo </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="grupo_sang" 
                                    name="grupo_sang_alumno" 
                                    placeholder="Si no lo sabe, ponga lo que quiera. " 
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    {
                                        ( errors.grupo_sang_alumno && touched.grupo_sang_alumno ) &&
                                            <div className="form-error m-1"> { errors.grupo_sang_alumno } </div>
                                    }
                            </div>
                        </div>

                        <div className="form-group">
                            <label className="control-label col-sm-2"> Teléfono fijo </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="tel_fijo" 
                                    name="tel_fijo_alumno" 
                                    placeholder="Si no lo sabe, ponga lo que quiera." 
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    {
                                        ( errors.tel_fijo_alumno && touched.tel_fijo_alumno ) &&
                                            <div className="form-error m-1"> { errors.tel_fijo_alumno } </div>
                                    }                                    
                            </div>
                        </div>
                        
                        <div className="form-group">
                            <label className="control-label col-sm-2"> Teléfono móvil </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="tel_movil" 
                                    name="tel_movil_alumno" 
                                    placeholder="2235955948" 
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    {
                                        ( errors.tel_movil_alumno && touched.tel_movil_alumno ) &&
                                            <div className="form-error m-1"> { errors.tel_movil_alumno } </div>
                                    }                                       
                            </div>
                        </div>
                        
                        <div className="form-group">
                            <label className="control-label col-sm-2"> DNI </label>
                            <div className="col-sm-10">
                                <Field 
                                    className="form-control" 
                                    id="dni_alumno" 
                                    name="dni_alumno"
                                    placeholder="43742273" 
                                    autoComplete="off"
                                    type="input"
                                    as={ TextField }
                                    />
                                    {
                                        ( errors.dni_alumno && touched.dni_alumno ) &&
                                            <div className="form-error m-1"> { errors.dni_alumno } </div>
                                    }                                     
                            </div>
                        </div>

                        <div className="form-group">
                            <div className="col-sm-offset-2 col-sm-10">
                                <Button variant="contained" color="primary" disabled={ isSubmitting } type="submit" > Enviar formulario </Button>
                            </div>
                        </div>

                    </Form>
                )}
            </Formik>
        </div>
    )
}
